<script>
export default {
  data() {
    return {
      loading: false,
      confirmData: {
        hallId: -1,
        hallName: '',
        offerings: [],
        totalPrice: 0,
        name: '',
        phone: '',
        idCard: '',
        address: '',
        wish: '',
        receiver: {
          name: '',
          phone: '',
          address: '',
          remark: ''
        }
      },
      shippingFee: 15
    }
  },
  computed: {
    totalPrice() {
      const basePrice = this.confirmData.totalPrice
      const shipping = this.confirmData.receiver && this.confirmData.receiver.name ? this.shippingFee : 0
      return basePrice + shipping
    }
  },
  onLoad() {
    // ‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑ÂèñÊï∞ÊçÆ
    const data = uni.getStorageSync('hallOrder')
    if (data) {
      this.confirmData = { ...this.confirmData, ...data }
    } else {
      // Â¶ÇÊûúÊ≤°ÊúâÊï∞ÊçÆÔºåËøîÂõû‰∏ä‰∏ÄÈ°µ
      uni.showToast({ 
        title: 'ËØ∑ÂÖàÂ°´ÂÜô‰æõÂìÅ‰ø°ÊÅØ', 
        icon: 'none',
        duration: 2000
      })
      setTimeout(() => {
        uni.navigateBack()
      }, 2000)
    }
  },
  methods: {
    getOfferingIcon(name) {
      const iconMap = {
        'È¶ô': 'üïØÔ∏è',
        'Ëä±': 'üå∏',
        'ÁÅØ': 'üí°',
        'Ê∞¥': 'üíß',
        'Êûú': 'üçé',
        'È£ü': 'üçö'
      }
      return iconMap[name] || 'üïØÔ∏è'
    },
    submitOrder() {
      this.loading = true
      
      // Ê®°ÊãüÊèê‰∫§ËÆ¢Âçï
      setTimeout(() => {
        this.loading = false
        
        // ‰øùÂ≠òËÆ¢Âçï‰ø°ÊÅØ
        const orderData = {
          ...this.confirmData,
          orderId: 'H' + Date.now(),
          status: 'pending',
          totalPrice: this.totalPrice,
          createTime: new Date().toISOString()
        }
        
        // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
        const orders = uni.getStorageSync('hallOrders') || []
        orders.unshift(orderData)
        uni.setStorageSync('hallOrders', orders)
        
        // Ê∏ÖÈô§‰∏¥Êó∂Êï∞ÊçÆ
        uni.removeStorageSync('hallOrder')
        
        uni.showToast({ 
          title: 'Êèê‰∫§ÊàêÂäü', 
          icon: 'success',
          duration: 1500
        })
        
        setTimeout(() => {
          // Ë∑≥ËΩ¨Âà∞ÊîØ‰ªòÈ°µÈù¢
          uni.navigateTo({
            url: '/pages/hall/pay',
            success: () => {
              console.log('Ë∑≥ËΩ¨Âà∞ÊîØ‰ªòÈ°µÈù¢ÊàêÂäü')
            },
            fail: (err) => {
              console.error('Ë∑≥ËΩ¨ÊîØ‰ªòÈ°µÈù¢Â§±Ë¥•:', err)
              // Â¶ÇÊûúË∑≥ËΩ¨Â§±Ë¥•ÔºåË∑≥ËΩ¨Âà∞ÊàêÂäüÈ°µÈù¢
              uni.navigateTo({
                url: '/pages/hall/success'
              })
            }
          })
        }, 1500)
      }, 1000)
    },
    goBack() {
      uni.navigateBack({
        success: () => {
          console.log('ËøîÂõû‰øÆÊîπÊàêÂäü')
        },
        fail: (err) => {
          console.error('ËøîÂõû‰øÆÊîπÂ§±Ë¥•:', err)
